import { Suspense, lazy } from "react";
const { createBrowserRouter } = require("react-router-dom");

const Loading = <div>Loading....</div>
const SignUp = lazy(() => import("../components/Login/SignUp"))
const SignIn = lazy(() => import("../components/Login/SignIn"))
const HomeManager = lazy(() => import("../components/Home/HomeManager"))
const Admin = lazy(() => import("../components/Adminpage/Dashboard"))


const root = createBrowserRouter([

         {
           path: "login/signup",
           element: <Suspense fallback={Loading}><SignUp/></Suspense>
         },

         {
           path: "login/signing",
           element: <Suspense fallback={Loading}><SignIn/></Suspense>
         },

         {
           path: "home/manager",
           element: <Suspense fallback={Loading}><HomeManager/></Suspense>
         },
         {
           path: "admin/dashboard",
           element: <Suspense fallback={Loading}><Admin/></Suspense>
         }

])


export default root;
